.input-check {
    display: none;
}
.check {
    display: flex;
    align-items: center;
    color: var(--textColor);
    &:not(:last-child) {
        margin-bottom: 12px;
    }
    &-disable {
        pointer-events: none;
        color: #9c9c9c;
    }
}
.input-check + .check.check-disable::before {
        background-color: var(--bgColor);
        color: var(--bgColor);
}
.input-check + .check::before {
    content: "\E00F";
    font-family: "icons";
    font-size: 6px;
    color: var(--White);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: var(--White);
    width: 14px;
    height: 14px;
    border: 1px solid var(--borderColor);
    margin-right: 7px;
    cursor: pointer;
    transition: all .2s;
}
.input-check:checked + .check::before {
    background-color: var(--brandColor);
    border-color: var(--brandColor);
}

.dropdown {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    font-weight: 600;
    line-height: 1.2;
    cursor: pointer;
    &-border {
        width: 60px;
        height: 30px;
        margin-left: 25px;
        border-radius: var(--borderRadius);
        border: 1px solid var(--iconColor);
    }
    &::before {
        content: "";
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: 2px;
    }
    span {
        display: flex;
        align-items: center;
    }
    i {
        font-size: 12px;
        margin-left: 9px;
        transform: rotate(-90deg);
    }
    ul {
        padding: 3px 0;
    }
    &-list {
        display: none;
        position: absolute;
        left: -1px;
        top: calc(100% + 2px);
        z-index: 2;
        width: 100%;
        background-color: var(--White);
        box-shadow: var(--Shadow);
        border-radius: var(--borderRadius);
        border: 1px solid var(--iconColor);
        li a {
            width: 100%;
            padding: 0 10px;
            margin: 0;
        }
    }
    &:hover {
        .dropdown-list {
            display: block;
            &__right {
                display: none;
            }
        }
        i {
            transform: rotate(90deg);
        }
    }
    .dropdown {
        &::before {
            height: 100%;
            width: 5px;
            left: 100%;
            top: 0;
        }
        &:hover {
            .dropdown-list__right {
                display: block;
            }
        }
    }
    &-without-bg {
        .dropdown-list {
            width: auto;
            min-width: 300px;
            top: calc(100% + 10px);
            left: -15px;
            &__right {
                width: auto;
                top: -3px;
                left: calc(100% + 5px);
            }
        }
        li {
            margin: 0;
            a, span {
                padding: 5px 15px;
            }
        }
        &::before {
            height: 10px;
        }
    }
}